import React, { useState } from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { styled } from "@mui/material/styles";

const CustomAccordion = styled(Accordion)(({ theme, expanded }) => ({
   backgroundColor: "#f9f9f9",
   border: expanded ? "1px solid #800000" : "1px solid #ccc",
   borderRadius: "8px",
   boxShadow: "none",
   transition: "all 0.6s ease", //  
   marginBottom: "1rem",
}));

const CustomSummary = styled(AccordionSummary)(({ theme }) => ({
   backgroundColor: "transparent",
   flexDirection: "row",
   "& .MuiAccordionSummary-expandIconWrapper.Mui-expanded": {
      transform: "rotate(45deg)",
      color: "#800000",
   },
   "& .MuiAccordionSummary-content": {
      marginLeft: theme.spacing(1),
      marginBottom: "0", //   
   },
   fontFamily: '"Euclid Circular A", sans-serif',
   color: "#000",
   padding: "1rem 2rem", //  
   fontSize: "1.2rem",
   transition: "color 0.3s",
   "&:hover": {
      color: "#800000",
   },
}));

const CustomDetails = styled(AccordionDetails)(({ theme }) => ({
   backgroundColor: "#f9f9f9",
   fontFamily: '"Euclid Circular A", sans-serif',
   fontSize: "1rem",
   color: "#555",
   padding: "0 2rem 1.5rem 2rem", //   
   transition: "all 0.6s ease",
}));

const FAQ = () => {
   const [expanded, setExpanded] = useState(false);

   const handleChange = (panel) => (event, isExpanded) => {
      setExpanded(isExpanded ? panel : false);
   };

   const faqData = [
      {
         question: "      ?",
         answer:
            "      7  14  ,     .",
      },
      {
         question: "     ?",
         answer:
            ",      .  .",
      },
      {
         question: "     ?",
         answer:
            ",       .      .",
      },
      {
         question: "   ?",
         answer:
            "         .",
      },
      {
         question: "       ?",
         answer:
            "    .      ,     -.",
      },
   ];

   return (
      <div
         style={{ maxWidth: "1200px", margin: "0 auto", padding: "4rem 2rem" }}
      >
         <Typography
            variant="h2"
            style={{
               fontFamily: '"Remains", "LC Chalk", cursive',
               fontSize: "2.5rem",
               color: "#000",
               marginBottom: "2rem",
               textAlign: "center",
               transition: "color 0.3s",
               cursor: "default",
            }}
            onMouseEnter={(e) => (e.target.style.color = "#800000")}
            onMouseLeave={(e) => (e.target.style.color = "#000")}
         >
              
         </Typography>

         {faqData.map((item, index) => (
            <CustomAccordion
               expanded={expanded === index}
               onChange={handleChange(index)}
            >
               <CustomSummary expandIcon={<ExpandMoreIcon />}>
                  {item.question}
               </CustomSummary>
               <CustomDetails>{item.answer}</CustomDetails>
            </CustomAccordion>
         ))}
      </div>
   );
};

export default FAQ;
